<template>
    <div class="messRepChi">
        <p class="p_four"><span>
                <el-popover placement="bottom" width="400" trigger="click">
                    <el-button slot="reference" >回复</el-button>
                    <el-tabs v-model="activeNameRep">
                        <el-tab-pane label="留言昵称" name="first">
                            <el-input :disabled="NameBolRep" type="text" placeholder="请输入昵称" v-model="nameTextRep" maxlength="10"
                            show-word-limit>
                            </el-input>
                            <span>
                            <span class="niming_bol">是否匿名</span>
                            <el-switch style="margin:30px;" v-model="NameBolRep" active-color="#13ce66" inactive-color="#ff4949">
                            </el-switch>
                            </span>
                        </el-tab-pane>
                        <el-tab-pane label="留言内容" name="second">
                            <!-- 这里要用原生的input 才能用ref监听到光标 -->
                            <input v-model="valueInputRep" placeholder="请输入留言内容" ref="inputboxRep" class="inputSpe" />

                            <el-popover placement="bottom" width="200" trigger="click">
                            <el-button slot="reference"><b>😀</b></el-button>
                            <el-tabs>
                                <!-- 将表情渲染到组件 -->
                                <b v-for="(item, index) of emoji" :key="index" @mouseup.capture="handlerEmojiRep"
                                style="cursor: pointer;user-select: none;">{{ item }}</b>
                            </el-tabs>
                            </el-popover>

                            <!-- </el-input> -->
                            <el-button type="primary" plain style="margin:10px;" @click="messRepChi" >留下消息</el-button>

                        </el-tab-pane>
                        </el-tabs>
                </el-popover>
            </span></p>
        <div class="p_five">
            <ul>
                <li v-for="(item,index) in repArrXuan" :key="index" class="messRepChi_li">
                  <p class="p_six">{{item.replyName}}</p>
                  <p class="p_seven">{{item.replyTime}}</p>
                  <p class="p_eight">{{item.replyValue}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
    name: 'messRepChi',
    props:['messId'],
    data() {
        return {
            activeNameRep:'first',
            //input框内容绑定
            nameTextRep:'',
            valueInputRep:'',
            //是否匿名
            NameBolRep:false,
            messRepArr:[],
            repArrXuan:[],
        }
    },
    created(){
        this.getMessRep()
        // this.repArrXuan = this.messRepArr
    },
    methods:{
        handlerEmojiRep(event) {
        //获取点击得到的emoji
        let emo = event.target.innerText
        //获取鼠标光标位置
        let inde = this.$refs.inputboxRep.selectionStart
        // console.log(this.$refs.inputbox.selectionStart);
        //将emoji输入到光标处
        this.valueInputRep = this.valueInputRep.substring(0,inde) + emo + this.valueInputRep.substring(inde)
        },
        //留言回复的点击事件
        messRepChi(){
            if(!this.valueInputRep) return this.$message.error('回复内容不能为空')
            async function outerB(e){
                if(e.NameBolRep){  //是匿名
                let {data} = await e.$axios.post('/node/message/messRepChi',{
                // repName:this.nameTextRep,
                repValue:e.valueInputRep,
                repId:e.messId
                })
                return data
                }else{  //否匿名
                    let {data} = await e.$axios.post('/node/message/messRepChi',{
                    repName:e.nameTextRep,
                    repValue:e.valueInputRep,
                    repId:e.messId
                })
                return data
                }
            }
            outerB(this).then(res=>{
                this.messRepArr.unshift(res)
                this.repArrXuan = this.messRepArr
                // console.log(res);
                this.$message.success('回复成功')
            })
            this.nameTextRep = ''
            this.valueInputRep = ''
            
        },
        async getMessRep(){
            let {data} = await this.$axios.post('/node/message/getMessRep',{id:this.messId})
            // console.log(data);
            this.messRepArr = data
            this.repArrXuan = this.messRepArr
        },
    },
    computed:{
        ...mapState(['emoji'])
    },
}
</script>

<style lang="less">
.p_four {
    text-align: right;
    font-size: smaller;

    span:hover {
        // color: red;
        cursor: pointer;
        // transition: 0.5s;
    }
}

.p_five {
    text-align: left;
    padding: 5px;
    // border: 1px solid #eee;
    // border-radius: 5px;
    ul .messRepChi_li{
        border-radius: 5px;
        border: 2px solid #eee;
        margin-top: 10px;
        padding: 10px;
        // background: pink;
        .p_six{
            font-size: large;
            font-weight: bolder;
            font-family: 'KaiTi';
        }
        .p_seven{
            margin-left: 25px;
            font-size: smaller;
            color: rgb(168, 168, 168);

        }
        .p_eight {
                margin-top: 10px;
                text-align: left;
                margin-left: 20px;
                padding: 5px;
                border: 1px solid #eee;
                border-radius: 5px;
            }
    }
}
.inputSpe {
        -webkit-appearance: none;
        background-color: #FFFFFF;
        border-radius: 4px;
        border: 1px solid #DCDFE6;
        box-sizing: border-box;
        color: #606266;
        display: inline-block;
        font-size: 0.9em;
        height: 40px;
        line-height: 40px;
        outline: 0;
        padding: 0 15px;
        transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
        width: 100%;
        &:focus{
          border: 1px solid rgb(64,158,255);
        }
      }
</style>