<template>
  <div class="Message">
    <h1>留言</h1>
    <div class="messageBox">
      <div class="writeBox">
        <el-tabs v-model="activeName">
          <el-tab-pane label="留言昵称" name="first">
            <el-input :disabled="NameBol" type="text" placeholder="请输入昵称" v-model="nameText" maxlength="10"
              show-word-limit>
            </el-input>
            <span>
              <span class="niming_bol">是否匿名</span>
              <el-switch style="margin:30px;" v-model="NameBol" active-color="#13ce66" inactive-color="#ff4949">
              </el-switch>
            </span>
          </el-tab-pane>
          <el-tab-pane label="留言内容" name="second">
            <!-- 这里要用原生的input 才能用ref监听到光标 -->
            <input v-model="valueInput" placeholder="请输入留言内容" ref="inputbox" class="inputSpe" />

            <el-popover placement="bottom" width="200" trigger="click">
              <el-button slot="reference"><b>😀</b></el-button>
              <el-tabs>
                <!-- 将表情渲染到组件 -->
                <b v-for="(item, index) of emoji" :key="index" @mouseup.capture="handlerEmoji"
                  style="cursor: pointer;user-select: none;">{{ item }}</b>
              </el-tabs>
            </el-popover>

            <!-- </el-input> -->
            <el-button type="primary" plain style="margin:10px;" @click="sendMessage">留下消息</el-button>

          </el-tab-pane>
        </el-tabs>
      </div>
      <messageReply :messageArr='messageArr'  />
    </div>
  </div>
</template>

<script>
//引入vuex
import { mapState } from 'vuex';
import messageReply from './messageChild/messageReply.vue'

export default {
  name: 'Message',
  components:{
    messageReply,
  },
  data() {
    return {
      activeName: 'first',
      //昵称与留言内容
      nameText: '',
      valueInput: '',
      //是否匿名的bol
      NameBol: false,
      messageArr:[],
      //拿到该条留言的id
      // messId:'',
    }
  },
  methods: {
    //设置留下留言方法
    async sendMessage() {
      // console.log(this.valueInput);
      //消息内容检测
      if (!this.valueInput) return this.$message.error('消息内容不能为空');

      //创建一个闭包，来获取块级作用域内的数据,虽然这里不太需要
      async function outer(e) {
        // e => this
        if (e.NameBol) {

          let { data } = await e.$axios.post('/node/message/sendMessage', {
            // visitorName:this.nameText,
            messageValue: e.valueInput
          })
          e.$message.success(data.value)
          // console.log(data.bol);
          return data.bol
        } else {

          let { data } = await e.$axios.post('/node/message/sendMessage', {
            visitorName: e.nameText,
            messageValue: e.valueInput
          })
          if (data.code) {
            e.$message.success(data.value)
            return data.bol
          }
        }
      }

      //将闭包获取的数据存入变量b  
      //返回的是一个期约，因此用then操作得到的数据
      outer(this).then(res=>{
        this.messageArr.unshift(res)
        console.log(this.messageArr,res._id);  //可以拿到id
        this.messId = res._id
      })
      // console.log(b);
      //发送完消息后清空input
      this.valueInput = ''
      this.nameText = ''

    },
    //将表情输入文本
    handlerEmoji(event) {
      //获取点击得到的emoji
      let emo = event.target.innerText
      //获取鼠标光标位置
      let inde = this.$refs.inputbox.selectionStart
      // console.log(this.$refs.inputbox.selectionStart);
      //将emoji输入到光标处
      this.valueInput = this.valueInput.substring(0,inde) + emo + this.valueInput.substring(inde)
    },
  },
  computed: {
    //获取vuex仓库数据
    ...mapState(['emoji'])
  },
}
</script>

<style lang="less">
.Message {
  padding: 10px;
  // height: 300px;

  h1 {
    font-family: "STKaiti";
    padding: 5px;
    background-image: linear-gradient(to right, white, rgb(93, 202, 245));
    margin-right: -12px;
  }

  .messageBox {
    margin-top: 20px;

    .writeBox {
      // margin-top: 20px;
      width: 96%;
      height: 120px;
      // background: pink;
      border: 1px solid rgb(93, 202, 245);
      border-radius: 10px;
      padding: 20px;
      padding-bottom: 50px;

      .niming_bol {
        background: #eee;
        // height: 0px;
        // border: 1px solid #eee;
        padding: 5px;
        border-radius: 8px;

      }

      .inputSpe {
        -webkit-appearance: none;
        background-color: #FFFFFF;
        border-radius: 4px;
        border: 1px solid #DCDFE6;
        box-sizing: border-box;
        color: #606266;
        display: inline-block;
        font-size: 0.9em;
        height: 40px;
        line-height: 40px;
        outline: 0;
        padding: 0 15px;
        transition: border-color .2s cubic-bezier(.645, .045, .355, 1);
        width: 100%;
        &:focus{
          border: 1px solid rgb(64,158,255);
        }
      }

    }
  }
}

.el-popover {
  text-align: center;
}

//设置input 的placeholder字体颜色兼容
// 谷歌
input::-webkit-input-placeholder {
  color: rgb(192, 196, 204);
}

// 火狐19+版本
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: rgb(192, 196, 204);
}

// 火狐4-18版本
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: rgb(192, 196, 204);
}

// IE10-11版本
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: rgb(192, 196, 204);
}
</style>