<template>
    <div class="messageReply">
        <ul class="message_ul_fir">
            <li v-for="(item, index) in messageXuan" :key="index" class="message_li_fir">
                <!-- v-for="(item,index) in messageArr" :key="index" -->
                <div class="message_logo"><img src="../../assets/img/visitor.webp"  alt=""></div>
                <div class="message_p">
                    <div class="mes_p_fir">
                        <p class="p_one">{{ item.visitorName }}</p>
                        <p class="p_two">{{ item.visitTime }}</p>
                        <p class="p_three">{{ item.messageValue }}</p>
                    </div>
                    <div class="mes_p_sec">
                        <!-- 回复子组件 -->
                        <messRepChi :messId="item._id" />
                    </div>
                </div>

            </li>

        </ul>
        <el-pagination background @current-change="currentChange" layout="prev, pager, next" :total="totall">
        </el-pagination>
    </div>
</template>

<script>
import messRepChi from './messageReplyChi/messRepChi.vue'
export default {
    name: 'messageReply',
    props: ['messageArr'],
    components:{messRepChi},
    data() {
        return {
            totall: 0,
            //存储所有已请求的留言信息
            messageAll: [],
            //渲染数组
            messageXuan: [],
        }
    },
    methods: {
        //请求留言数据
        async getMessages() {
            //发送ajax请求
            let { data } = await this.$axios.get('/node/message/getMessageFP')
            // console.log(data);
            //留言数据
            let FirMess = data.bol
            //这是页数
            this.totall = Math.ceil(data.b / 3) * 10
            this.messageAll = new Array(this.totall * 3)
            this.messageArr.push(...FirMess)
            this.messageAll.splice(0, 3, ...FirMess)
        },
        async currentChange(pageNum) {
            //this.messageXuan = this.messageAll.slice((pageNum - 1) * 3 , pageNum * 3)           //!!! 数据undeefined报错 解决cnm!
            if (this.messageAll[(pageNum - 1) * 3]) return this.messageXuan = this.messageAll.slice((pageNum - 1) * 3 , pageNum * 3)
            let { data } = await this.$axios.post('/node/message/getMessageOnwhich', {
                pageNum
            })
            // console.log(data);
            //存入数据缓存用，避免每次翻页都请求数据
            this.messageAll.splice((pageNum - 1) * 3, 3, ...data)
            //渲染数据
            this.messageXuan = data
        },
    },
    created() {
        this.getMessages()
        this.messageXuan = this.messageArr
    },
}
</script>

<style lang="less">
.messageReply {
    margin-top: 20px;
    // height: 400px;
    // border: 1px solid pink;

    .message_ul_fir .message_li_fir {
        width: 100%;
        // height: 200px;
        border: 1px solid rgb(93, 202, 245);
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-radius: 10px;
        display: flex;

        .message_logo {
            margin-top: 20px;
            margin-left: 20px;
            width: 100px;
            height: 100px;

            img {
                width: 100%;
                height: 100%;
                border-radius: 50px;
                box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
            }
        }

        .message_p {
            width: 70%;
            margin-top: 20px;
            margin-left: 20px;

            .p_one {
                font-size: larger;
                font-weight: bolder;
                font-family: 'KaiTi';
                text-align: left;
                margin-left: -20px;
            }

            .p_two {
                font-size: small;
                color: rgb(168, 168, 168);
                // margin-left: -600px;
                text-align: left;
                margin-bottom: 10px;
            }

            .p_three {
                text-align: left;
                padding: 5px;
                border: 2px solid #eee;
                border-radius: 5px;
                margin-bottom: 10px;
            }

            
        }
    }
}
</style>